import error from '../../components/scodeError.vue'
export default {
	components: {
		error
	},
	data() {
		return {
			title: '你好！',
			type: 'scode',
			text: '扫码错误',
			showError: false,
			zscode: '',
			flag:'',
			activity:''
		}
	},
	onLoad() {

















































































	},
	destroyed() {
		this.activity.unregisterReceiver(this.flag);//取消监听  
	},
	onHide() {
		 



	},
	onUnload() {
		 



	},
	methods: {
		createdClick() {
			this.scode()
		},
		confirm() {
			this.$common.get("/trace-api/trace/changeOutCode?outCode=" + this.zscode).then((res) => {
				console.log(res)
				if (Number(res.data.code) === 200) {
					uni.showToast({
						title: res.data.message,
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 1500)
				} else {
					uni.showToast({
						title: res.data.message,
						duration: 2000,
						icon: "none"
					});
				}
			})
		},
		scode() {
			uni.scanCode({
				success: (res) => {

					if (res.result && res.result.indexOf("https://2641.cn/") > -1) {
						let sid = res.result.split("https://2641.cn/")[1]
						this.getCodeZsNumber(sid)
					} else {
						this.showError = true
						this.text = "内码获取失败"
						this.type = 'scode'
					}
				}
			});
		},
		getCodeZsNumber(sid) {
			let that = this
			this.$common.get("/trace-api/trace/getSubCodeById?sid=" + Number(sid)).then((res) => {
				console.log(res)
				if (Number(res.data.code) === 200) {
					if (res.data.data.isLeaf === 'Y') {
						that.$common.showToast("扫码成功", "success")
						that.showError = false
						that.zscode = res.data.data.traceSubCodeNumber || ""
					} else {

						this.showError = true
						this.text = "此码不是内码,内码获取失败"
						this.type = 'scode'
					}

				} else {
					that.showError = false
					that.$common.showToast(res.data.message, 'none')
				}
			})
		}
	}
}
